.main__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem
}

.main__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.main__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.main__navbar-links_container p,
.main__navbar-menu_container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.main__navbar-menu{
    margin-left: 1rem;

    display: none;
    position: relative;
}

.main__navbar-menu svg{
    cursor: pointer;
}

.main__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;

    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}


.main__navbar-menu_container p{
    margin: 1rem 0;
}

@media screen and (max-width: 1050px){
    .main__navbar-links_container{
        display: none;
    }

    .main__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .main__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 550px){
    .main__navbar{
        padding: 2rem
    }
}

.main__navbar-menu_container{
    top: 20px;
}